form{
    display: block;
    margin-block-end: 1em;
    width: 250px;
    height: 250px;
    position: absolute;
    top: 50%;
    left: 50%;
    margin: -125px 0 0 -125px;
}

table{
    margin-left: -10px;
    margin-top: 15px;
    background-color: #E7FFCC;
    color: #121212;
    padding: 2px;
    border: 1px solid silver;

    display: table;
    border-collapse: separate;
    box-sizing: border-box;
    text-indent: initial;
    white-space: normal;
    line-height: normal;
    font-weight: normal;
    font-size: medium;
    font-style: normal;
    text-align: start;
    border-spacing: 2px;
    font-variant: normal;
}